<template>
  <div>
    <div class="searchs">
      <el-form
        :inline="true"
        @submit.native.prevent="onSubmit"
        :model="searchs"
        class="form-search"
      >
        <el-form-item label="搜索">
          <el-input
            v-model="searchs.keyword_search_value"
            placeholder="会员手机号"
          ></el-input>
        </el-form-item>
        <el-form-item label="类型">
          <el-select v-model="searchs.search_type" clearable placeholder="类型">
            <el-option label="查看上级" value="1"></el-option>
            <el-option label="查看下级" value="2"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" native-type="submit">查询</el-button>
        </el-form-item>
      </el-form>
    </div>
    <el-table :data="tableData" class="list" v-loading="loading">
      <el-table-column prop="uid" label="编号" align="center" />
      <el-table-column
        prop="mobile"
        label="手机号"
        align="center"
        width="120"
      />
      <el-table-column prop="money" label="余额" align="center" />
      <el-table-column prop="status_name" label="用户状态" align="center" />
      <el-table-column
        prop="reg_ip"
        label="注册IP"
        align="center"
        width="150px"
      />
      <el-table-column
        prop="reg_addr"
        label="注册地址"
        align="center"
        width="150px"
      />
      <el-table-column
        prop="last_login_ip"
        label="最近登录IP"
        align="center"
        width="150px"
      />
      <el-table-column
        prop="last_login_addr"
        label="最近登录地址"
        align="center"
        width="150px"
      />
      <el-table-column prop="name" label="姓名" align="center" />
      <el-table-column prop="is_auth_name" label="是否认证" align="center" />
      <el-table-column
        prop="top_name"
        label="推荐人姓名"
        align="center"
        width="120"
      />
      <el-table-column
        prop="top_mobile"
        label="推荐人手机号"
        align="center"
        width="120"
      />
    </el-table>
    <el-pagination
      background
      layout="total, prev, pager, next"
      :total="count"
      :current-page="page_curren"
      :hide-on-single-page="false"
      @current-change="currentChange"
      class="pagination"
    />
  </div>
</template>
<script>
import Fecth from '../../utils/fetch'

export default {
  data() {
    return {
      searchs: {
        keyword_search_value: '',
        search_type: '2',
      },
      keyword_search_value: '',
      tableData: [],
      count: 0,
      page_curren: 1,
      loading: false,
    }
  },
  mounted() {
    this.FetchList()
  },
  methods: {
    FetchList() {
      this.loading = true
      Fecth('/user/top/search', {
        page_curren: this.page_curren,
        ...this.searchs,
      })
        .then(r => {
          this.loading = false
          this.count = r.data.count
          this.page_curren = Math.floor(r.data.page_curren)
          this.tableData = r.data.list
        })
        .catch(() => (this.loading = false))
    },
    currentChange(page) {
      this.page_curren = page
      this.FetchList()
    },
    onSubmit() {
      this.page_curren = 1
      this.FetchList()
    },
  },
}
</script>
